.img-outreach-event {
  border-radius: 0px 0px 10px 0px;
  height: 490px;
  width: 45%;
  padding: 0;
  background-position: center;
  background-image: url("../../../images/outreach/bgc_speaker_event.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  float: right;
  transition: all 0.1s ease;
}

.img-bgc-event {
  background-image: url("../../../images/outreach/bgc_speaker_event.jpg");
}

.img-sts-event {
