.name-card {
  height: fit-content;
  width: 220px;
  margin: 10px;
  padding: 15px;
  border: 1px solid;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.name-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
}

.name-card-dark {
  border-color: #444;
  background-color: rgba(40, 40, 40, 0.8);
  color: #f0f0f0;
}

.name-card-light {
  border-color: #ddd;
  background-color: rgba(250, 250, 250, 0.8);
  color: #333;
}

.name-card-dark:hover {
  border-color: orange;
  background-color: rgba(50, 50, 50, 0.9);
}

.name-card-light:hover {
  border-color: coral;
  background-color: rgba(255, 255, 255, 0.9);
}

.caption {
  width: 100%;
  padding: 5px 0;
  line-height: 1.5;
  text-align: left;
  display: flex;
  flex-direction: column;
}

.name {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.caption > .roles {
  width: 100%;
  display: block;
  font-size: 13px;
  line-height: 1.4;
  opacity: 0.85;
}
