.divider {
  width: 80% !important;
  margin-inline: 10%;
  margin-bottom: 30px;
  height: 2px;
  background: white;
  outline: none;
  border: none;
}

/* ANIMATION FOR POPUP */
.face-container {
  position: relative;
  overflow: hidden;
}

.face-overlay {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 10px 5px;
  background: rgba(0, 0, 0, 0.75);
  color: white;
  text-align: center;
  font-size: 0.85rem;
  line-height: 1.3;
  transform: translateY(100%);
  transition: transform 0.3s ease;
  word-break: keep-all;
}

/*slides popup*/
.face-container:hover .face-overlay {
  transform: translateY(0);
}
/* ------------------------------------------------------------------------- */
