@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');
.home-bg-img {
  background-image: url('../../../images/home_tessellation.png');
  background-size: 100px;
  filter: brightness(26%);
  /* General attributes */
  position: fixed;
  left: 0;
  right: 0;
  z-index: 1;
  display: block;
  height: 100%;
  z-index: -1;
}

.home-container {
  width: 100vw;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-container {
  display: flex;
  flex-wrap: wrap;
}

.post-hero-container {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.article-centered-home {
  width: calc(100vw - 300px);
}

.first-box-margin {
  margin-top: 30px;
}

.hero-no-img {
  height: 50vw;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  overflow: hidden;
}

.hero-img {
  position: relative;
  height: 50vw;
  width: 100vw;
  overflow: hidden;
}

.hero-img-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(231, 117, 0, 0.6), rgba(0, 0, 0, 0.6), rgba(3, 162, 220, 0.6));
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  -webkit-animation: fade 48s infinite, grow 24s infinite;
  animation: fade 48s infinite, grow 24s infinite;
  -webkit-animation-timing-function: ease, linear;
  animation-timing-function: ease, linear;
}

