@-webkit-keyframes fade {
  0% {
    opacity: 1;
  }
  12.5% {
    opacity: 1;
  }
  16.7% {
    opacity: 0;
  }
  95.8% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    opacity: 1;
  }
  12.5% {
    opacity: 1;
  }
  16.7% {
    opacity: 0;
  }
  95.8% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes grow {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes grow {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.hero-logo {
  height: 150vw;
  width: 150vw;
  opacity: 25%;
  background-image: url(../../../images/home/PURC\ Logo\ Blue.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
}

.hero-text {
  padding: 1vw 2vw 1vw 2vw;
  font-family: 'Century Gothic';
  font-family: 'Roboto Mono', monospace;
  font-size: 4vw;
  color: black;
  background-color: white;
  /* text-transform: uppercase; */
  z-index: 1;
}

.hero-word {
  font-family: 'Roboto Mono', 'Courier New', monospace;
  /* text-transform: lowercase; */
  font-size: 5vw;
  color: rgb(231, 117, 0);
}

.hero-bar {
  font-size: 5vw;
  color: rgba(0, 0, 0, 0);
}

.hero-subtext {
  padding: 1vw 2vw 1vw 2vw;
  font-family: 'Century Gothic';
  font-size: 3vw;
  color: white;
  text-transform: uppercase;
  z-index: 1;
  font-weight: bold;
  text-align: center;
}

.text-droid {
  color: rgb(227, 77, 0);
}

.text-pacbot {
  color: rgb(255, 183, 0);
}

.text-golf {
  color: rgb(48, 191, 134);
}

.text-drone {
  color: rgb(0, 126, 255);
}

.text-exo {
  color: rgb(170, 48, 191);
}

.text-rover {
  color: rgb(191, 48, 103);
}

.text-robocup {
  color: rgb(58, 185, 169);
}

.text-tinker {
  color: rgb(181, 27, 20);
}

.text-wall-e {
  color: rgb(244, 183, 0);
}

.text-bionics {
  color: rgb(222, 64, 199);
}

/* ------------------------------------------------------------------------- */
/*                                  MOBILE                                   */
/* ------------------------------------------------------------------------- */

@media screen and (max-width: 960px) {
  .article-centered-home {
    width: calc(100vw - 50px);
  }

  .hero-no-img {
    height: 150vw;
  }

  .hero-img {
    height: 150vw;
  }

  .hero-logo {
    height: 300vw;
    width: 300vw;
  }

  .hero-text {
    padding: 2vw 4vw 2vw 4vw;
    font-size: 8vw;
  }

  .hero-word {
    font-size: 8.5vw;
  }

  .hero-subtext {
    padding: 2vw 4vw 2vw 4vw;
    font-size: 6vw;
  }
}